<template>
  <div id="app">
    <k-header :ifHeaderTit='ifHeaderTit' @turnStatus='turnStatusFun'></k-header>
    <tabLink></tabLink>
    <router-view/>
  </div>
</template>

<script type="text/ecmascript-6">
import KHeader from './components/k-header/k-header'
import tabLink from './components/tabLink/tabLink'
export default {
  data() {
    return {
      ifHeaderTit:true
    }
  },
  components:{
    KHeader,
    tabLink
  },
  methods:{
     turnStatusFun(){
        this.ifHeaderTit=!this.ifHeaderTit
     }
  }
}
</script>

<style>
   @import './common/scss/base.scss'

</style>



